html,
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  min-width: 300px;
}

body {
  color: #131a20;
  background-color: #fcfcf7;
}

.animate,
.animate * {
  transition: all 360ms ease-in-out;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.dark {
  color: #fff;
  background-color: #131a20;
}

button {
  outline: none;
  min-width: 234px;
  cursor: pointer;
  padding: 0 16px;
  margin: 10px;
  height: 50px;
  border-radius: 6px;
  font-size: 24px;
  font-weight: 700;
  border: 0px;
  color: #fff;
  background: -webkit-linear-gradient(#e8d612, #ffa035);
}

.dark button {
  color: #fff;
  background: -webkit-linear-gradient(#2f74e2, #7a99f4);
}

span {
  font-weight: 600;
}

h1 {
  background: -webkit-linear-gradient(#e8d612, #ffa035);
  font-weight: 900;
  margin-bottom: 20px;
  text-align: center;
  font-size: 150px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 300ms ease-in;
}

h2 {
  font-size: 36px;
  text-align: center;
  font-weight: 400;
  background: -webkit-linear-gradient(#e8d612, #ffa035);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 300ms ease-in;
}

.dark h1,
.dark h2,
.dark h3 {
  background: -webkit-linear-gradient(#2f74e2, #7a99f4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

h3 {
  background: -webkit-linear-gradient(#e8d612, #ffa035);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  margin: 14px 0;
  font-size: 25px;
  transition: all 300ms ease-in;
}

p {
  margin: 12px 0;
  font-size: 18px;
}

@media (max-width: 620px) {
  h1 {
    font-size: 62px;
  }

  h2 {
    font-size: 26px;
  }
}
